import React from 'react';
import { Link } from 'react-router-dom';

function Banner(){
    return <section id="banner">
        <br/>
        <br/>
        <br/>
        <br/>

        <div className="container">
          <div className="row">

            <div className="col-lg-6">
              <h1>Um aplicativo voltado para pequenas empresas.</h1>
              <h4>Gerencie suas contas em um único lugar.</h4>
              <Link to="/novaconta" className="btn btn-success btn-lg btn-banner">Criar uma Conta</Link>
              <Link to="/app" className="btn btn-outline-secondary btn-lg btn-banner">Fazer Login</Link>
            </div>

            <div className="col-lg-6">
              <img src="/images/screenshot-crm.png" alt="teste"/>
            </div>
          </div>
        </div>
    </section>
    
    
  }

export default Banner;
