import React from 'react';
import {Link} from 'react-router-dom';
import './novaconta.css';



function NovaConta() {

  return <div className="d-flex align-items-center text-center form-container container-fluid" >
    <div className="col-md-6">
      <form className="form-signin">
        <h1 className="h3 mb-3 fw-normal">Criar Conta</h1>

        <div className="form-floating">
          <input type="email" className="form-control" id="floatingInput" placeholder="Email" />
          <label for="floatingInput">Email</label>
        </div>

        <div className="form-floating">
          <input type="password" className="form-control" id="floatingPassword" placeholder="Senha" />
          <label for="floatingPassword">Senha</label>
        </div>

        <button className="w-100 btn btn-lg btn-primary" type="submit">Criar Conta</button>

        <div className="login-links mt-5">
          <Link to="/app" className="mx-3">Já tenhho uma conta.</Link>
        </div>
        
      </form>

    </div>

    <div className="col-md-6">
      <h3 className="text-info lg-6">Terms and Conditions</h3>
      <p className="text-left">
        By clicking on "Register" you agree to The Company's' Terms and Conditions
      </p>
      <p>
        While rare, prices are subject to change based on exchange rate fluctuations -
        should such a fluctuation happen, we may request an additional payment. You have the option to request a full refund or to pay the new price. (Paragraph 13.5.8)
      </p>
      <p>
        Should there be an error in the description or pricing of a product, we will provide you with a full refund (Paragraph 13.5.6)
      </p>
      <p>
        Acceptance of an order by us is dependent on our suppliers ability to provide the product. (Paragraph 13.5.6)
      </p>

    </div>
  </div>


}

export default NovaConta;