import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';

/* Páginas */
import Site from './site/site';
import Login from './app/Views/Login/login';
import NovaConta from './app/Views/Login/novaconta';
import ResetSenha from './app/Views/Login/resetsenha';
import Home from './app/Views/Home/home';
import NovoCliente from './app/Views/Clientes/novocliente';

function App(){
    return <BrowserRouter>
          <Route exact path='/' component={Site} />
          <Route exact path='/app' component={Login} />
          <Route exact path='/novaconta' component={NovaConta} />
          <Route exact path='/resetsenha' component={ResetSenha} />
          <Route exact path='/home' component={Home} />
          <Route exact path='/novocliente' component={NovoCliente} />

        </BrowserRouter>;
  }

export default App;